.container {
  min-height: 100vh;
  color: white;
  background-color: black;
}

.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1rem;
}

.bg-dark {
  background-color: black;
}

.container h1 {
  color: red;
  padding: 0 2rem;
}

.container header {
  border-bottom: 1px white solid;
}

.container form label {
  margin-right: 10px;
  font-weight: bold;
}

.container form input {
  padding: 5px 20px;
  border-radius: 5px;
  margin-right: 10px;
}

.container form button {
  background-color: red;
  border: none;
  border-radius: 10px;
  padding: 5px 15px;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

.container form button:hover {
  opacity: 0.7;
}

.tvShows {
  display: grid;
  gap: 10px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  justify-items: center;
  align-items: center;
  margin: 2rem 0 0;
}

.w-30 {
  width: 200px;
}

.noResult {
  text-align: center;
  margin-top: 2rem;
}

.text-center {
  text-align: center;
}

.cardTitle {
  font-weight: bold;
  color: darkgray;
}

.score {
  margin-left: 5px;
  font-size: 15px;
}

.tvCard {
  border: 1px solid white;
  box-sizing: border-box;
  cursor: pointer;
  height: 430px;
  margin-bottom: 30px;
}

.tvCard:hover {
  opacity: 0.7;
}

.dialog {
  padding: 0;
  border: 0;
  border-radius: 0.6rem;
  box-shadow: 0 0 1em black;
  z-index: 1;
}

@media (max-width: 800px) {
  .tvShows {
    grid-template-columns: 1fr;
  }

  .header {
    padding: 1rem;
    display: block;
    text-align: center;
  }
}
